body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-radial-gradient(circle, #29648A 0%, #25274D 100%);
  background: radial-gradient(circle, #29648A 0%, #25274D 100%);
}
@font-face {
font-family: 'SEGA LOGO FONT';
font-style: normal;
font-weight: normal;
src: local('SEGA LOGO FONT'), url(/reactjs-robot-search/static/media/SEGA.12a3db8b.woff) format('woff');
}

h1 {
    font-family: 'SEGA LOGO FONT';
    font-weight: 200;
    color: #2E9CCA;
}

#logo {
    width: 140px;
    margin: 6px;
    -webkit-transition: all .2s;
    transition: all .2s;
}

#logo:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
